import { HttpClient } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Category, Food, Restaurant } from 'src/Table/table';
import { CategoryService } from '../Service/category.service';
import { FoodService } from '../Service/food.service';
import { OrderService } from '../Service/order.service';
import { RestaurantService } from '../Service/restaurant.service';
import { SharedService } from '../Service/shared.service';
import { BehaviorSubject } from 'rxjs';
import { IonContent, ModalController } from '@ionic/angular';
import { FormBuilder, Validators } from '@angular/forms';
@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {
  regform = this.fb.group({});
  listOfRestaurant: any[] = [];
  listOfFood: Food[];
  categories = [];
  highlights = [];
  featured = [];
  term: string;
  featuredSlideOpts = {
    slidesPerView: 1.2,
    spaceBetween: 10,
    freeMode: true
  };
  showLocationDetail = false;
  filteredItemsSearch: any;
  cartItemCount: BehaviorSubject<number>;
  cart = [];
  @ViewChild('pageTop') pageTop: IonContent;
  listOfCatagoryFilter: BehaviorSubject<any>;
  count: number = 0;
  listOfCatagory: any[] = [];
  categoryIds: any[];
  listOfRestaurants: Restaurant[];
  menu: boolean = false;
  category: any[] = [];
  categoriesList: Category[];
  public pageScroller() {
    this.pageTop.scrollToTop();
  }
  currentDate = new Date().toISOString();
  constructor(
    private fb: FormBuilder,
    private restaurantService: RestaurantService,
    private foodService: FoodService,
    private catagoryService: CategoryService,
    private router: Router,
    private sharedService: SharedService,
    private http: HttpClient,
    private orderService: OrderService,
    private modelControler: ModalController) {
    console.log(this.currentDate);
    var date = new Date(this.currentDate);
    var d = Date.parse(this.currentDate);
    var hours = date.getHours() > 12 ? date.getHours() - 12 : date.getHours();
    var am_pm = date.getHours() >= 12 ? "pm" : "am";
    var minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
    let time = hours + ":" + minutes + " " + am_pm;
  }
  ngOnInit() {
    // this.http.get('https://devdactic.fra1.digitaloceanspaces.com/foodui/home.json').subscribe((res: any) => {
    //   this.categories = res.categories;
    //   this.highlights = res.highlights;
    //   this.featured = res.featured;
    // });
    this.regform = this.fb.group({
      menu: [""]
    })
    this.cartItemCount = this.orderService.getCartItemCount();
    this.cart = this.orderService.getCart();
    this.getAllRestaurant();
    this.getCatagory();
    this.getRestaurant();
    this.getFood();
  }
  getCatagory() {
    this.catagoryService.getAllCategory().subscribe(res => {
      this.categoriesList = res;
      res.forEach(element => {
        this.listOfRestaurants.forEach(ele => {
          if (ele.category)
            for (let i = 0; i < ele.category.length; i++) {
              if (ele.category[i] == element.id) {
                this.count = this.count + 1;
              }
            }
        })
        let data = {
          id: element.id,
          categoryName: element.categoryName,
          count: this.count
        }
        this.listOfCatagory.push(data);
        this.count = 0;
        // console.log(this.listOfCatagory);
      });
    })
  }
  selectMenu(event) {
    this.listOfRestaurant = [];
    this.categoryIds = event.detail.value;
    if (this.categoryIds.length > 0) {
      this.listOfRestaurants.forEach(ele => {
        if (ele.category.length > 0) {
          this.count = 0;
          for (let i = 0; i < ele.category.length; i++) {
            if (this.categoryIds.length > 0) {
              for (let j = 0; j < this.categoryIds.length; j++) {
                if (ele.category[i] == this.categoryIds[j]) {
                  this.count = this.count + 1;
                }
              }
            }
          }
          if (this.count == 1) {
            this.catagoryService.getAllCategory().subscribe(result => {
              this.categoriesList = result;
              this.category = [];
              for (let i = 0; i < ele.category.length; i++) {
                let data = {
                  categoryName: this.categoriesList.find(c => c.id == ele.category[i]).categoryName,
                }
                this.category.push(data);
              }
              let data = {
                id: ele.id,
                Name: ele.Name,
                Location: ele.Location,
                Type: ele.Type,
                StartWorkingHour: ele.StartWorkingHour,
                EndWorkingHour: ele.EndWorkingHour,
                AreaSpace: ele.AreaSpace,
                PhoneNumber: ele.PhoneNumber,
                GM: ele.GM,
                ContactPerson: ele.ContactPerson,
                Email: ele.Email,
                Photo: ele.Photo,
                accountId: ele.accountId,
                status: ele.status,
                Category: this.category
              }
              this.listOfRestaurant.push(data);
            });
          }
        }
      })
    }
    else {
      this.restaurantService.getAllRestaurant().subscribe(res => {
        res.forEach(ele => {
          this.catagoryService.getAllCategory().subscribe(result => {
            this.categoriesList = result;
            this.category = [];
            for (let i = 0; i < ele.category.length; i++) {
              let data = {
                categoryName: this.categoriesList.find(c => c.id == ele.category[i]).categoryName,
              }
              this.category.push(data);
            }
            let data = {
              id: ele.id,
              Name: ele.Name,
              Location: ele.Location,
              Type: ele.Type,
              StartWorkingHour: ele.StartWorkingHour,
              EndWorkingHour: ele.EndWorkingHour,
              AreaSpace: ele.AreaSpace,
              PhoneNumber: ele.PhoneNumber,
              GM: ele.GM,
              ContactPerson: ele.ContactPerson,
              Email: ele.Email,
              Photo: ele.Photo,
              accountId: ele.accountId,
              status: ele.status,
              Category: this.category
            }
            this.listOfRestaurant.push(data);
          });
        });
      });
    }
  }
  getAllRestaurant() {
    this.restaurantService.getAllRestaurant().subscribe(res => {
      this.listOfRestaurants = res;
    });
  }
  getRestaurant() {
    this.restaurantService.getAllRestaurant().subscribe(res => {
      res.forEach(ele => {
        this.catagoryService.getAllCategory().subscribe(result => {
          this.categoriesList = result;
          this.category = [];
          for (let i = 0; i < ele.category.length; i++) {
            let data = {
              categoryName: this.categoriesList.find(c => c.id == ele.category[i]).categoryName,
            }
            this.category.push(data);
          }
          let data = {
            id: ele.id,
            Name: ele.Name,
            Location: ele.Location,
            Type: ele.Type,
            StartWorkingHour: ele.StartWorkingHour,
            EndWorkingHour: ele.EndWorkingHour,
            AreaSpace: ele.AreaSpace,
            PhoneNumber: ele.PhoneNumber,
            GM: ele.GM,
            ContactPerson: ele.ContactPerson,
            Email: ele.Email,
            Photo: ele.Photo,
            accountId: ele.accountId,
            status: ele.status,
            Category: this.category
          }
          this.listOfRestaurant.push(data);
        });
      });
    });
  }
  getFood() {
    this.foodService.getAllFood().subscribe(res => {
      this.listOfFood = res;
    })
  }
  // Dummy refresher function
  doRefresh(event) {
    setTimeout(() => {
      event.target.complete();
    }, 2000);
  }
  openLink(id, resId) {
    this.sharedService.resId.next(resId);
    this.router.navigate(["/menu/details/" + id]);
  }
  // show or hide a location string later
  onScroll(ev) {
    const offset = ev.detail.scrollTop;
    this.showLocationDetail = offset > 40;
  }
  filter(query) {
    this.filteredItemsSearch = (query.target.value) ?
      this.listOfRestaurant.filter(p => p.Name.toLowerCase().includes(query.target.value.toLowerCase())) :
      this.listOfRestaurant;
  }
  filterByMenu() {
    this.menu = true;
  }
  selectOpenClose(ev){
    this.listOfRestaurant = [];
   this.restaurantService.getAllRestaurant().subscribe(res => {
    res.forEach(ele => {
      this.catagoryService.getAllCategory().subscribe(result => {
        this.categoriesList = result;
        this.category = [];
        for (let i = 0; i < ele.category.length; i++) {
          let data = {
            categoryName: this.categoriesList.find(c => c.id == ele.category[i]).categoryName,
          }
          this.category.push(data);
        }
        let data = {
          id: ele.id,
          Name: ele.Name,
          Location: ele.Location,
          Type: ele.Type,
          StartWorkingHour: ele.StartWorkingHour,
          EndWorkingHour: ele.EndWorkingHour,
          AreaSpace: ele.AreaSpace,
          PhoneNumber: ele.PhoneNumber,
          GM: ele.GM,
          ContactPerson: ele.ContactPerson,
          Email: ele.Email,
          Photo: ele.Photo,
          accountId: ele.accountId,
          status: ele.status,
          Category: this.category
        }
        this.listOfRestaurant.push(data);
        this.listOfRestaurant = this.listOfRestaurant.filter(c=>c.status==ev.detail.value);
      });
    });
  });
  }
}
